<template>
  <div class="boardList">
    <h2>자유게시판</h2>
    <b-table striped hover :fields="fields" :items="items"></b-table>
    <input type="text" v-model="bno"/>
    <b-button type="button" @click="searchList">조회</b-button>
  </div>
</template>

<script>
export default {
  data(){
    return {
        fields:[
          {key : 'bno', label : '번호'},
          {key : 'title', label : '제목'},
          {key : 'content', label : '내용'},
          {key : 'cuser', label : '작성자'},
          {key : 'cdate', label : '작성일'},
          {key : 'cuser', label : '수정일'},
        ],
        items: [],
        bno:''
    };
  },
  mounted(){
    this.$nextTick(function(){ // 모든 화면이 렌더링된 후에 실행
      this.$axios.get('http://localhost:8080/board/selectList')
        .then(response =>{
          this.items = response.data
          console.log(this.items);
        })
        .catch(err => {
          console.log(err);
        });
    }) 
  }
  ,
  computed:{
    hasResult:function(){
      return this.items.length > 0
    }
  },
  methods:{
    searchList(){
        this.$axios.post('http://localhost:8080/board/searchList')
        .then(response =>{
          this.items = response.data
          console.log(this.items);
        })
        .catch(err => {
          console.log(err);
        });      
      }
    }
  }
</script>