<template>
  <div class="boardList">
    <h2>자유게시판</h2>
    <div>
      <b-table striped hover :fields="fields" :items="items"></b-table>

      <b-form inline>
        <b-form-select
          id="inline-form"
          v-model="condition.key"
          :options="conditions"
        ></b-form-select>

        <b-form-input
          id="inline-form-input-condition"
          v-model="condition.value"
          class="mb-2 mr-sm-2 mb-sm-0"
        ></b-form-input>

        <b-button @click="searchList" variant="primary">검색</b-button>
      </b-form>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return {
        fields:[
          {key : 'bno', label : '번호'},
          {key : 'title', label : '제목'},
          {key : 'cuser', label : '작성자'},
          {key : 'cdate', label : '작성일'},
          {key : 'cuser', label : '수정일'},
        ],
        items: [],
        conditions:[
          {text: '전체', value: 'null'},
          {text: '작성자', value: 'cuser'},
          {text: '제목', value: 'title'}
        ],
        condition:{key : '', value : ''}
    };
  },
  mounted(){
    this.$nextTick(function(){ // 모든 화면이 렌더링된 후에 실행
      this.$axios.get('http://localhost:8080/board/selectList')
        .then(response =>{
          this.items = response.data
          console.log(this.items);
        })
        .catch(err => {
          console.log(err);
        });
    }) 
  }
  ,
  computed:{
    hasResult:function(){
      return this.items.length > 0
    }
  },
  methods:{
    searchList(){
        this.$axios.post('http://localhost:8080/board/searchList',
          this.condition.key == 'cuser' ? {cuser : this.condition.value} : {title : this.condition.value}
        )
        .then(response =>{
          this.items = response.data
          console.log(this.items);
        })
        .catch(err => {
          console.log(err);
        });   
      },
      test(){
        console.log(this.condition);
        console.log(this.conditions);
      }
    }
  }
</script>